@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?ov6qhh');
  src:  url('../fonts/icomoon.eot?ov6qhh#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?ov6qhh') format('truetype'),
    url('../fonts/icomoon.woff?ov6qhh') format('woff'),
    url('../fonts/icomoon.svg?ov6qhh#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-instagram-brands:before {
  content: "\e900";
}
.icon-facebook-f-brands:before {
  content: "\e901";
}
.icon-linkedin-in-brands:before {
  content: "\e902";
}
.icon-github-brands:before {
  content: "\e903";
}
.icon-xmark-solid:before {
